@import '../../libraries/wp-conditions/assets/css/wp-conditions';

/* Hide 'visibility' from admin */
.post-type-advanced_pricing .misc-pub-visibility#visibility {
	display: none;
}


.post-type-product .wpc-conditions {
	.wpc-condition-wrap {
		.wpc-value-field-wrap input { float: none; display: inline-block; }
		.wpc-condition { width: 35% !important; }
		.wpc-operator { width: 22.5% !important; }
		.wpc-value { width: calc( 42.5% - 75px ) !important; min-width: 0; }
	}
	.wpc-condition-group { margin: 0 -20px 1.5em -14px; }
}

.post-type-product .woocommerce-advanced-pricing-condition {
	input { min-width: 0 !important; padding: 0 8px !important; }
}
.post-type-advanced_pricing, .post-type-product {
	div#tiptip_content td ~ td {
		text-align: left;
		float: left;
		padding-left: 15px;
	}

	div#tiptip_content tr > td {
		text-align: right;
	}
}

/******************************
 * Settings meta box
 ******************************/
#wcapri_settings {

	.wcapri-option label {
		display: inline-block;
		width: 150px;
	}
	.description {
		vertical-align: middle;
		display: inline-block;
	}

	/* Bulk pricing */
	.head {
		margin-bottom: 5px;
	}
	.bulk-row {
		margin-bottom: 5px;
	}
	.bulk-price-wrap input {
		width: 100%;
		box-sizing: content-box;
		padding: 0;
		margin: 0;
	}
	.bulk-row .bulk-row-field,
	.bulk-row-heading {
		display: inline-block;
		width: 25%;
		margin-right: .3%;
	}
	.bulk-row .description {
		float: right;
	}
	.bulk-row-add {
		margin-top: 0.5em;
		margin-right: 63px;
		margin-left: calc( 75% - 83px );

		&:before {
			font-family: 'dashicons';
			content: "\f502";
			vertical-align: middle;
			height: 1.2em;
			line-height: 1;
			display: inline-block;
			margin-right: 5px;
			color: #667;
		}
	}
	.bulk-row-delete {
		opacity: 0;
		transition: opacity .2s ease-in-out;
		box-sizing: content-box;
	}
	.bulk-row:hover .bulk-row-delete {
		opacity: 1;
	}
}


/******************************
 * Product tab
 ******************************/
#advanced_pricing_data {

	.add_pricing_condition {
		float: right;
		margin: 0 0 0 6px;
	}
	.add_pricing_condition_select {
		float: right;
		margin: 0 0 0 6px;
	}
	.toolbar-add label {
		float: right;
		line-height: 30px;
	}

	.advanced-pricing-rule-title-wrap {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;

		.pricing_name {
			font-weight: bold;
			flex: 1 1 auto;
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
		}
		.remove_row,
		.handlediv {
			justify-self: flex-end;
			flex-shrink: 0;
		}
	}
	.advanced-pricing-title {
		border-right: 1px solid #EEE;
		margin-right: 10px;
		padding-right: 10px;
	}


	.woocommerce-help-tip {
		height: 30px;
		line-height: 30px
	}
	.woocommerce-help-tip:after {
		line-height: 30px
	}

	// Bulk rows
	.bulk-row {

		&:hover .bulk-row-delete {
			opacity: 1;
		}
		.bulk-row-delete {
			display: flex;
			align-items: center;
			opacity: 0;
			transition: opacity .2s ease-in-out;
			padding: 0 5px;
		}

		.bulk-row-field.bulk-row-amount input {
			width: calc(100% - 35px) !important;
		}
	}
}

#woocommerce-product-data ul.wc-tabs .pricing_options.active:hover > a:before,
#woocommerce-product-data ul.wc-tabs .pricing_options > a:before {
	content: "\f18e";
}

/***************************************
 * Custom single pricing settings
 **************************************/

.wpc-currency-wrap {
	display: flex;

	.wpc-currency {
		line-height: 2em;
		margin: 0 -1px 0 0;
		padding: 0 10px;
		font-size: 14px;
		font-weight: 400;
		color: #555;
		background-color: #EEE;
		border: 1px solid #7e8993;
		border-radius: 4px 0 0 4px;
		height: unset !important;
	}
	input {
		flex-grow: 1;
		line-height: 2em;
		margin-left: -1px;
		padding: 0 8px !important;
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
	}
}
